

const Skor = ({ currentStreak, currentStreakScore, highestStreak, highestStreakScore }) => {

  return (
    <div className="skor">
      <p className="skor-detay current-streak">Gncel Seri: {currentStreak}</p>
      <p className="skor-detay current-streak-score">Gncel Seri Skoru: {currentStreakScore}</p>
      <p className="skor-detay highest-streak">En Yksek Seri: {highestStreak}</p>
      <p className="skor-detay highest-streak-score">En Yksek Seri Skoru: {highestStreakScore}</p>
    </div>
  );
}

export default Skor;